import React, { useState } from "react";
import style from "./searchbar.module.css";
import { FaSearch } from "react-icons/fa";
import { searchIt} from "../../Redux/actions";
import { connect } from "react-redux";
import searchDogs from '../../functions/search';
import { getDogs } from "../../Redux/actions";

function SearchBar({Dogs,searchIt,getDogs}) {

    const [input, setInput] = useState('');
    const [error, setError] = useState('');

    function handleChange(e){
        if(!/(^$)|[a-zA-Z]/.test(e.target.value)){
            setError("Please just enter letters");
        }
        else{
            setError("");
            setInput(e.target.value);
            if(e.target.value){
              searchIt(Dogs,e.target.value);
            }else{
              getDogs(1);
            }
        }
    }

    function handleSubmit(e){
        e.preventDefault();
        if(!input){
            return setError("Nothing to Submit");
        }
    }

  return (
    <div>
      <form className={style.fromContainer} onSubmit={handleSubmit}>
        <div className={style.searchBarContainer}>
            {error && <p className={style.error}>{error}</p>}
          <input
            type="text"
            placeholder="Type to search!!"
            className={style.input}
            onChange={handleChange}
          />
          <div className={style.search}>
            <button type="submit">
              <FaSearch />
            </button>
          </div>
        </div>
      </form>
    </div>
  );
}


function mapStateToProps(state) {
  return {
    Dogs: state.Dogs
  };
}

function mapDipatchToProps(dispatch) {
  return {
    searchIt: (dogs,input) => dispatch(searchIt(dogs,input)),
    getDogs: (page) => dispatch(getDogs(page)),
  };
}

export default connect(mapStateToProps, mapDipatchToProps)(SearchBar);
