import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import Card from "../Card/card.js";
import { getDogs } from "../../Redux/actions";
import style from "./dogs.module.css";
import Search from "../SearchBar/searchbar.js";
const image = "https://static.wixstatic.com/media/760dfc_84266ca3cce4402290a46a7145e1de57~mv2.jpg/v1/fill/w_340,h_220,al_c,q_80,usm_0.66_1.00_0.01/Pets%20Picture%20Not%20Available.webp"

function Dogs(props) {
  const [page, setpage] = useState(1);

  useEffect(() => {
    props.getDogs(page);
  }, [page]);
  

  return (
    <>
      <div>
        <Search />
      </div>
      <div className={style.Dogs_container}>
        
        
        {props.Dogs[0] ? (
          props.Dogs.map((m) => {
            if(m.error){
              return (
                <p key={m.error}>Sorry there's no Dogs to show</p>
              )
            }
            return (
              <Card
                key={m.id}
                id={m.id}
                name={m.name}
                img={m.image ? m.image.url : image }
                temperament={m.temperament}
              />
            );
          })
        ) : (
          <h1>Loading.....</h1>
        )}
      </div>
      <div className={style.pagination}>
        {page === 1 ? (
          <button disabled={true}>BACK</button>
        ) : (
          <button onClick={() => setpage(page - 1)}>BACK</button>
        )}
        {/* //DESABILITAR CUANDO YA NO HAY MAS PAGINAS */}
        <button onClick={() => setpage(page + 1)}>NEXT</button>
      </div>
    </>
  );
}

function mapStateToProps(state) {
  return {
    Dogs: state.Dogs,
    search: state.search
  };
}

function mapDipatchToProps(dispatch) {
  return {
    getDogs: (page) => dispatch(getDogs(page)),
  };
}

export default connect(mapStateToProps, mapDipatchToProps)(Dogs);
