import {show_dogs, get_temperaments, get_detail,search} from '../actions';
import paginated from "../../functions/paginated";
import searchDogs from '../../functions/search';

const InitialState = {
    Dogs: [],
    details:{},
    temperaments:[],
}

const todos = (state = InitialState, action) => {
    switch (action.type) {
        case (show_dogs): {
            return {
                ...state,
                Dogs: paginated(action.payload,action.page)
            }
        }
        case(get_temperaments): {
            return {
                ...state,
                temperaments: action.payload
            }
        }
        case(get_detail): {
            return {
                ...state,
                details: action.payload
            }
        }
        case(search): {
            return {
                ...state,
                Dogs: searchDogs(action.dogs, action.input)
            }
        }
        default : return state
    }
}

export default todos;